<template>
  <div class="input-wrapper">
    <textarea
      name="userInputBet"
      id="userInputBet"
      @input="checkUserInputBet"
      v-model="userInputBet"
      cols="30"
      rows="5"
    ></textarea>
    <div class="random-numbers clear-fix">
      <a
        v-for="(randomItem, randomIndex) in randomInputNumbers"
        @click="randomInputCounts(randomItem.value)"
        :key="randomIndex"
        href="javascript:;"
        >{{ $t(randomItem.label) }}</a
      >
    </div>
    <div class="clear-fix">
      <p>{{ $t("gameTemplate.tips1") }}</p>
      <a @click="clearUserInputBet" href="javascript:;">{{
        $t("gameTemplate.empty")
      }}</a>
      <a @click="filterUserInput" href="javascript:;">{{
        $t("gameTemplate.filter")
      }}</a>
    </div>
  </div>
</template>

<script>
import { SET_SSC_SELECTED_COUNTS } from "../../../store/nameSpace";
import inputReset from "../../credit/mixins/input-reset";
import minxinsLrylBtnControl from "../../credit/minxins-lrylbtncontrolfalse";
import { mapGetters } from "vuex";
import { checkLotteryCounts } from "../../../common/js/LotteryBet";
import { randomInputNumbers } from "../../../config/game-default-config";

export default {
  mixins: [inputReset, minxinsLrylBtnControl],
  props: [
    "flagCount",
    "getShoppingCarList",
    "nowPlayWayInfo",
    "nowActiveWayInfoDetail"
  ],
  data() {
    return {
      randomInputNumbers,
      userInputBet: "",
      selectedCounts: 0
    };
  },
  computed: {
    ...mapGetters(["nowActiveGameName"])
  },
  methods: {
    filterUserInput() {
      if (!this.userInputBet) return;
      const result = checkLotteryCounts({
        betData: this.userInputBet,
        areaSplitChar: this.nowActiveWayInfoDetail["area_split_char"],
        ballSplitChar: this.nowActiveWayInfoDetail["ball_split_char"],
        methodDetail: this.nowActiveWayInfoDetail["count_function"]
      });
      if (!result.sourceData.length) {
        this.userInputBet = "";
        return;
      }
      let areaSplitChar =
        this.nowActiveWayInfoDetail["area_split_char"] === "j"
          ? ";"
          : this.nowActiveWayInfoDetail["area_split_char"];
      let ballSplitChar =
        this.nowActiveWayInfoDetail["ball_split_char"] === "j"
          ? ";"
          : this.nowActiveWayInfoDetail["ball_split_char"];
      if (this.nowActiveGameName === "GGBALOTO") {
        this.userInputBet =
          result.sourceData
            .map(item => {
              return (
                item.slice(0, item.length - 1).join(ballSplitChar) +
                "+" +
                item[item.length - 1]
              );
            })
            .join(areaSplitChar) + areaSplitChar;
      } else {
        this.userInputBet =
          result.sourceData
            .map(item => item.join(ballSplitChar))
            .join(areaSplitChar) + areaSplitChar;
      }
      this.$message({
        message: "号码已按照标准格式进行重置",
        type: "success",
        duration: 1000
      });
    },
    checkUserInputBet() {
      this.userInputBet = this.userInputBet.replace(
        /[^(\d|,|，|;|；|\s|+|＋|-|－|\\|｜\n)]/,
        ""
      );
      const result = checkLotteryCounts({
        betData: this.userInputBet,
        areaSplitChar: this.nowActiveWayInfoDetail["area_split_char"],
        ballSplitChar: this.nowActiveWayInfoDetail["ball_split_char"],
        methodDetail: this.nowActiveWayInfoDetail["count_function"]
      });

      if (result.betCount && result.isRepeatNumber) {
        let areaSplitChar =
          this.nowActiveWayInfoDetail["area_split_char"] === "j"
            ? ";"
            : this.nowActiveWayInfoDetail["area_split_char"];
        let ballSplitChar =
          this.nowActiveWayInfoDetail["ball_split_char"] === "j"
            ? ";"
            : this.nowActiveWayInfoDetail["ball_split_char"];
        if (this.nowActiveGameName === "GGBALOTO") {
          this.userInputBet =
            result.sourceData
              .map(item => {
                return (
                  item.slice(0, item.length - 1).join(ballSplitChar) +
                  "+" +
                  item[item.length - 1]
                );
              })
              .join(areaSplitChar) + areaSplitChar;
        } else {
          this.userInputBet =
            result.sourceData
              .map(item => item.join(ballSplitChar))
              .join(areaSplitChar) + areaSplitChar;
        }
        this.$message({
          message: "检测到有重复或不合法的号码, 已按照正确格式重置",
          type: "error",
          duration: 1000
        });
      }
      result.labelInfo = result.numberStr;
      this.selectedCounts = result.betCount;
      this.$store.commit(SET_SSC_SELECTED_COUNTS, this.selectedCounts);
      return result;
    },
    randomInputCounts(counts) {
      this.userInputBet = checkLotteryCounts({
        betData: "",
        areaSplitChar: this.nowActiveWayInfoDetail["area_split_char"],
        ballSplitChar: this.nowActiveWayInfoDetail["ball_split_char"],
        methodDetail: this.nowActiveWayInfoDetail["count_function"],
        isCreateByRandom: counts
      }).numberStr;
      this.checkUserInputBet();
    }
  },
  watch: {
    flagCount() {
      const result = this.checkUserInputBet();
      if (result.result !== 0) {
        return this.getShoppingCarList(false, false, 0);
      }
      this.getShoppingCarList(
        result.numberStr,
        this.selectedCounts,
        undefined,
        result.labelInfo
      );
    }
  },
  created() {}
};
</script>

<style scoped>
/* @import "../../../statics/ssc-plate.css"; */
@import "../statics/css/plate.css";
</style>
